* {
  margin: 0;
}

body {
  font-family: Roboto, sans-serif;
}

.card {
  box-sizing: border-box;
  background: url("imac.202c52ff.jpeg") 50% 32px / 160px 134px no-repeat;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  height: 408px;
  padding-left: 16px;
  padding-right: 16px;
}

.card__name {
  color: #060b35;
  margin-top: 206px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  margin-top: 4px;
  font-family: Roboto, sans-serif;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__review {
  justify-content: space-between;
  align-items: center;
  height: 16px;
  margin-top: 15.5px;
  display: flex;
}

.card__review-txt {
  color: #060b35;
  text-align: right;
  font-family: Roboto, sans-serif;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  margin-top: 24px;
  display: flex;
}

.card__price-txt {
  color: #616070;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price-amount {
  text-align: right;
  color: #060b35;
  font-family: Roboto, sans-serif;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button-link {
  color: #fff;
  background-color: #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  height: 40px;
  margin-top: 16px;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: flex;
}

.card__button-link:hover {
  box-sizing: border-box;
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
  border-radius: 5px;
}

.stars {
  justify-content: space-between;
  width: 96px;
  display: flex;
}

.stars__star {
  content: "";
  background-image: url("star.822d561e.svg");
  width: 12px;
  height: 12px;
}

.stars--4 .stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}
